.eyesright { float: right; }

/** jqGrid **/
.ui-jqgrid .ui-jqgrid-view input,
.ui-jqgrid .ui-jqgrid-view select,
.ui-jqgrid .ui-jqgrid-view textarea,
.ui-jqgrid .ui-jqgrid-view button {
  font-size: .9em;
}

td span.fa-folder, span.fa-folder-open {
  cursor: pointer;
}

td.form-view-data > span > div {
  max-height: 150px;
  overflow: auto
}

#model-body img {
  display:block;
  margin: auto;
}

td.ocrPreview {
  cursor: pointer;
}

.ui-jqgrid tr.ui-row-ltr td.thumbPreview {
  text-align:center;
  vertical-align:middle;
}
.ui-jqgrid tr.ui-row-ltr td.thumbPreview a {
  display: inline-block;
  margin: 0 10px 0 10px;
}

.ui-th-ltr {
  background-color: @gray-lighter-light;
  color: #000;
}

td.buttons-sm {
  width: 410px;
}

td.buttons-xs {
  width: 150px;
}